import { useCallback, useMemo, useRef, useState } from "react";
import { CancelIcon } from "../icons/cancel";
import "./form.css";
import { useStore } from "../store";
import { useShallow } from "zustand/react/shallow";
import { Tooltip, TooltipRefProps } from "react-tooltip";
import { useTranslation } from "react-i18next";

export const Form = () => {
  const { t } = useTranslation();

  const [roads, isPlaying, wrongGuesses, startGame, cancelGame, submitGuess] =
    useStore(
      useShallow((state) => [
        state.roads,
        state.isPlaying,
        state.wrongGuesses,
        state.startGame,
        state.cancelGame,
        state.submitGuess,
      ]),
    );

  const score = useMemo(
    () => ({
      correct: roads.filter((r) => r.state === "correct").length,
      wrong: wrongGuesses,
      total: roads.length,
    }),
    [roads, wrongGuesses],
  );

  const tooltipRef = useRef<TooltipRefProps | null>(null);

  const [isCorrect, setIsCorrect] = useState(false);
  const [isWrong, setIsWrong] = useState(false);

  const onKeyDown = useCallback(
    (event) => {
      if (event.keyCode === 13) {
        const { result, road } = submitGuess(event.target.value);
        const fn = result ? setIsCorrect : setIsWrong;
        fn(true);
        setTimeout(() => fn(false), 1000);

        if (!result) {
          tooltipRef.current?.open({
            anchorSelect: "#guess-wrapper",
            content: road,
          });
          tooltipRef.current?.close({ delay: 2000 });
        }

        event.target.value = "";
      }
    },
    [setIsCorrect, setIsWrong, submitGuess],
  );

  return (
    <div
      className="p-5"
      style={{
        zIndex: 1000000,
        position: "absolute",
        bottom: "0",
        right: "0",
        left: "0",
      }}
    >
      {isPlaying ? (
        <div>
          <p className="tag mb-4">
            {score.correct} / {score.total}
            {score.wrong ? ` (${score.wrong} wrong)` : ""}
          </p>
          <div style={{ display: "flex", gap: "8px" }}>
            <div id="guess-wrapper" style={{ width: "100%" }}>
              <input
                type="text"
                placeholder={t("controls.guess")}
                onKeyDown={onKeyDown}
                autoFocus
                className={`input text-input ${isCorrect ? "correct" : ""} ${isWrong ? "wrong" : ""}`}
              />
            </div>
            <button className="button" onClick={cancelGame}>
              <span className="icon is-small">
                <CancelIcon color="#fff" />
              </span>
            </button>
          </div>
          <Tooltip ref={tooltipRef} className="box" />
        </div>
      ) : (
        <button className="button is-fullwidth" onClick={startGame}>
          {t("controls.start")}
        </button>
      )}
    </div>
  );
};
